import wPicker from "@/components/w-picker/w-picker.vue";
export default{
	components:{
		wPicker
	},
	data(){
		return{
			type:0,//0为企业招聘  1求职信息
			distype:true ,//true固定薪资  false 面议
			phone:'',//手机号码
			name:'',//公司名称
			address:'',//地址信息
			title:'',//标题
			content:'',//内容
			minMoney:'',//最低薪资
			maxMoney:'',//最大薪资
			wordname:'',//求职者姓名
			sex:0,//性别 0：男    1：女
			birthbay:'请选择',//出生年月
			wordphone:'',//求职者联系方式
		}
	},
	methods:{
		//生日选择开关
		shopTypeClick(){
			this.$refs.date.show();
		},
		//生日选择
		onConfirm(val){
			console.log(val);
			//如果页面需要调用多个mode类型，可以根据mode处理结果渲染到哪里;
			// switch(this.mode){
			// 	case "date":
			// 		break;
			// }
			console.log(val)
		},
		//点击企业招聘
		leftClick(){
			this.type = 0
		},
		//求职
		rightClick(){
			this.type = 1
		},
		//是否面议
		discussClick(){
			this.distype = !this.distype
		},
		//点击女
		sexnvClick(){
			this.sex =  1
		},
		//点击男
		sexnanClick(){
			this.sex =  0
		}
	}
}
